.color-nav {
	background: rgba(7, 7, 6, 0.931);
	box-shadow: 1px -.5px 3px .5px rgb(122, 101, 9, 3);
}

.text-nav {
	/* display: flex; */
	font-family: 'K2D', sans-serif;
	font-size: 1.2em;
	font-weight: bold;
	color: rgb(250, 248, 248);
}

.mir {
	font-size: 1.6em;
	text-shadow:
		-2px 2px 0 #000,
		1px 4px 0 #000;
}

.size {
	height: 4em;
}

@media screen and (max-width: 600px) {
	.text-nav {
		font-size: calc(1.3em - .3vmin);
	}
	.mir {
		font-size: calc(1.7em - .2vmin);
	}
	.garcia-nav {
		display: none;
	}
	nav ul a {
		padding: 0px 5px !important;
	}
}
.aboutme {
	color: #FFFFFF;
	margin-top: 5em;
	width: 100%;
	height: 130vh;
}

.text-body {
	font-family: 'Montserrat', sans-serif;
	font-size: calc(1em + 1vmin); 
	text-align: justify;
}


.photo-mir {
	height: 50vh;
	margin-top: 1.5em;
	border-radius: 3em;
	box-shadow: 10px 10px 5px 3px rgb(7, 7, 6, 0.671);
}

.mir-about {
	text-align: left;
}

.shadow {
	box-shadow: 1px -.5px 3px .5px rgb(7, 7, 6, 0.671);
}

.devicon {
	font-size: 7em;
	padding: 0.7rem .75rem !important;
	color: #a3a3a2;
}

.habilidades {
	text-align: center;
	font-family: 'Montserrat', sans-serif;
	font-size: 2em;
	margin-top: 3em;
}

@media screen and (max-width: 460px){
	.aboutme {
		height: 135vh !important;
		/* margin */

	}
}

@media screen and (max-width: 772px) {
	.aboutme {
		height: 130vh;
		margin-bottom: 5em;
	}
	.text-body {
		font-size: calc(.9em + 1vmin);
	}
	.photo-mir {
		display: flex;
		height: 35vh;
	}
}
@media screen and (max-width: 899px) {
	.aboutme {
		height: 100vh;
	}
}

@media screen and (min-width:900px) and (max-width: 1110px){
	.aboutme {
		height: 155vh;
	}
}


.background-home {
    background: url(/static/media/fondo.51c89902.png);
    background-size: cover;
    background-repeat: no-repeat;
    width: 100%;
    height: 100vh;
}
.background-div {
    width: 100%;
    height: 53vh;
    margin-top: 2em;
    border-radius: 2em
}


.photo-home {
    width: 100%;
    height: 50vh;
    margin-top: 2.5em;
    border-radius: 2em;
}

.text-home {
    margin-top: 5em;
	font-family: 'K2D', sans-serif;
	font-size: .9m;
	font-weight: bold;
    color: rgb(250, 248, 248);
	text-shadow:
		-1px 1px 0 #000,
		1px 3px 0 #000;
}

@media screen and (max-width: 1000px) {
    .photo-home {
        margin-top: .5em;
        height: 230px;
    }
    .text-home {
        margin-top: .5em;
    }
    .background-div{
        height: 250px;
    }
}

@media screen and (max-width: 1200px) and (min-height: 1366px) {
    .photo-home{
        height: 200px;
    }
    .background-div,.text-home{
        margin-top: 30em;
    }
}

.projects {
	font-family: 'Montserrat', sans-serif;
	width: 100%;
	height: 130vh;
	/* margin-bottom: 5em; */
}
.title-project{
	color: #ffffff;
	text-align: center;
	font-size: 2em;
	margin-top: 3em;
}

.card{
	height: 350px;
	color: black;
}

.title-projects{
	font-size: 1.9em;
	margin-bottom: 1em;
	color: #ffffff;
}

.c-click-text {
	color: #ffffff !important;
	font-size: .7rem;
	font-style: oblique;
}
.c-title {
	font-size: 1rem;
	font-weight: bold;
	margin: .5rem;
}

@media screen and (max-width: 420px) {
	.projects{
		/* margin-top: 40em; */
		height: 150% !important;
	}
	.card {
		height: 340px !important;
	}
}

@media screen and (min-width: 421px) and (max-width: 600px) {
	.projects {
		margin-top: 1em;
		height: 100%;
	}

	.c-title {
		font-size: 1.2rem;
	}
	.card {
		height: 450px !important;
	}
}

@media screen and (max-width: 900px){
	.projects{
		margin-top: 35em;
		height: 150% !important;
	}
	.card {
		height: 300px;
		width: 105%;
	}
} 


